$break-large:640px;
$break-content:420px;
.menu-item {
  min-width: 30%;
  height: 240px;
  flex: 1 1 ;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 7.5px 15px;
  background-position:center;
  
  background-size:contain;
  overflow: hidden;

  transition: transform 4s linear;
  
  &:first-child {
    margin-right: 7.5px;
  }

  &:last-child {
    margin-left: 7.5px;
  }
  .background-image {
    width: 100%;
    height: 100%;
  }
  &:hover {
    cursor: pointer;
    & .background-image {
      transform: scale(1.2);
      transition: transform 4s linear;
    }
    .content {
      opacity: 0.9;
    }
  }
  .content {
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: white;
    opacity: 0.7;
    position: absolute;

    .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
    }

    .subtitle {
      font-weight: lighter;
      font-size: 16px;
    }
    
  }
  @media screen and (min-width: $break-large) {
    &:nth-child(4){
      height: 380px;
    }
    &:nth-child(5){
      height: 380px;
    }
         
  }
  @media screen and (max-width: $break-content) {
    .content{
      padding:0px ;
      .title{
        padding:4px;
        font-size:12px;
      } 
      .subtitle{
        padding:10px;
        font-size:12px; 
      } 
    } 
    
 }

}
