$break-collectionItem:640px;
$break-collectionMin:420px;
.collection-item {
  width: 22%;
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center;
  margin-bottom: 3rem;
  position: relative;

  
  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }
  &:hover {
     opacity:.8;

    .CustomButton {
      display: block;
      transition: 1s linear;
      &:hover {
        background-color:black;
        opacity: .9;
      }
    }
  }
  .CustomButton {
    display:none;
    width: 80%;
    position: absolute;
    top: 255px;
    background-color:rgb(138, 132, 132);
    color: white;
    line-height: 3rem;
    border-radius: 0.2rem;
    cursor: pointer;
    border: none;
    
  }
  .collection-footer {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;

    .name {
      width: 90%;
      margin-bottom: 15px;
    }

    .price {
      width: 10%;
    }
  }
  @media screen and (max-width: $break-collectionItem) {
    width:45%; 
  }
  @media screen and (max-width: $break-collectionMin) {
    
      width:45%; 
      height:200px;
    .CustomButton {
      display:none;
      width: 80%;
      position: absolute;
      top: 130px;
      background-color:rgb(138, 132, 132);
      color: white;
      line-height: 2rem;
      border-radius: 0.2rem;
      cursor: pointer;
      border: none;
      }
      .collection-footer{
        font-size:10px;
      }
   }
  

}
