.submit-button {
  display: inline-block;
  background-color:rgb(151, 135, 135);
  color: blanchedalmond;
  border-style: none;

  padding: 1rem;
  text-decoration: solid;
  font-weight: bold;
  border-radius: 0.5rem;
  margin: 0.2rem 0.5rem;
  &:hover {
    background-color:rgb(54, 48, 48);
    color:whitesmoke;
    cursor: pointer;
  }
  &.Inverted {
    background-color: whitesmoke;
    color: black;
    opacity: 0.9;
    cursor: pointer;
    &:hover {
      background-color: black;
      color: blanchedalmond;
    }
  }
  &.googleSignIn {
    background-color: rgb(0, 195, 255);
    &:hover {
      background-color:rgb(54, 48, 48);
      color:whitesmoke;
      cursor: pointer;
    }
  }
}
