$break-signIn:880px;
$break-MSize:500px;
$break-small:408px;
.SignIn-SignOut {
  width: 80vw;
  display: flex;
  justify-content: space-around;
  margin: 0rem auto;
  .sign-up{
    display:none;   
     
  }
  @media screen and (max-width: $break-signIn) {
   
   .signIn{
    width:75%;   
      
   }    
     
  } 
  @media screen and (max-width: $break-MSize) {
   
    .signIn{
     width:80%;   
       
    }    
      
   }
   @media screen and (max-width: $break-small) {
   
    .signIn{
     width:100%;   
       
    }    
      
   }
}
