$break-checkoutMax: 800px;
$break-checkoutMin: 460px;
$break-small: 360px;
.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;

  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1.5px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;
      font-size: 1rem;
      &:last-child {
        width: 8%;
      }
    }
  }
  button {
    margin-left: auto;
    margin-top: 25px;
    margin-bottom: 20px;
  }
  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }
  .signIn-alert {
    margin: 2rem auto;
    font-weight: bold;
    font-size: 20px;
    .SignOut-Links {
      color: rgb(185, 185, 185);
      margin-left: 10px;
      &:hover {
        color: rgb(87, 83, 83);
        text-decoration: underline;
      }
    }
  }

  @media screen and (max-width: $break-checkoutMax) {
    width: 90%;
    .checkout-header {
      .header-block {
        font-size: 14px;
      }
    }
  }
  @media screen and (max-width: $break-checkoutMin) {
    width: 100%;
    .checkout-header {
      .header-block {
        font-size: 10px;
      }
    }
    .checkout-item {
      span {
        font-size: 10px;
      }
    }
  }
  @media screen and (max-width: $break-small) {
    width: 100%;

    .checkout-item {
      .image-container {
        width: 100px;
      }
    }
  }
}
