$break-SignUpM:598px;
$break-SignUpS:420px;
.sign-up {
  margin:0 auto;
  width: 60%;
  .SignOut-Link{
    .SignOut-Links{
      color:rgb(185, 185, 185);
      &:hover{
        color:rgb(87, 83, 83);
        text-decoration: underline;
      }    
    }
 }
  @media screen and (max-width: $break-SignUpM) {
    
    width:80%;
      
  }
  @media screen and (max-width: $break-SignUpS) {
    
    width:100%;
      
  }
}
